@import '../common/styles/variable';

.footer {
    background-color: $bgColorSecond;
    .wrapperFooter{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        padding: 40px 0 ;
        .title {
            color: $titleColor;
            font-weight: 500;
            font-size: 1rem;
        }
        .listItem {
            display: flex;
            justify-content: space-between;
            align-items: center;
            list-style-type: none;
            gap: 10px;
            padding: 0;
            .item {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $mainColor;
                width: 40px;
                height: 40px;
                border-radius: 5px;
                .link{
                    display: flex;
                    cursor: pointer;
                }
            }
        }
        .rights {
            font-size: 12px;
            color: $mainColor;
        }
    }
}
